<template>
  <div class="home col-6">
    <h1>"Welcome to Your Vue.js App"</h1>

    <!-- Check that the SDK client is not currently loading before accessing is methods -->
    <div class="col-12" v-if="!$auth.loading">
      <!-- show login when not authenticated -->
      <button class="form-control" v-if="!$auth.isAuthenticated" @click="login">Log in</button>
      <!-- show logout when authenticated -->
      <button class="form-control" v-if="$auth.isAuthenticated" @click="logout">Log out</button>
    </div>
    <div class="col-12" v-if="$auth.isAuthenticated">
      <div>
        <img :src="$auth.user.picture">
        <h2>{{ $auth.user.name }}</h2>
        <p>{{ $auth.user.email }}</p>
      </div>

      <div>
        <pre>{{ JSON.stringify($auth.user, null, 2) }}</pre>
      </div>
    </div>
  </div>
</template>
<script>
// .. imports removed for brevity

export default {
  name: "home",
  methods: {
    // Log the user in
    login() {
      this.$auth.loginWithRedirect();
    },
    // Log the user out
    logout() {
      this.$auth.logout({
        //returnTo: window.location.origin
        returnTo: 'https://request.msc.edu.hk/#/auth/login'
      });
    }
  }
};
</script>


